.testimonials__container {
  width: 40%;
  padding-bottom: 4rem;
}

.testimonial {
  background: var(--color-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  user-select: none;
  margin-bottom: 2rem;
}

.client__avatar {
  width: 4rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.4rem solid var(--color-primary-variant);
}

.client__review {
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 90%;
  margin: 0.8rem auto 0;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
  .testimonials__container {
    width: 60%;
  }
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
  .testimonials__container {
    width: var(--container-width-sm);
    padding-bottom: 3rem;
  }

  .client__review {
    width: 100%;
    font-size: 0.85rem;
  }
  
  .testimonial {
    padding: 1.5rem;
  }
}
